@import url(https://fonts.googleapis.com/css2?family=Nerko+One&family=Open+Sans+Condensed:wght@300&display=swap);
.Home .lander {
    padding: 80px 0;
    text-align: center;
  }
  
  .Home .lander h1 {
    font-weight: 800;
  }
  .BO-landing-image {
    height: 40vmin;
    pointer-events: none;
  }
@media all and (min-width: 480px) {
  .Login {
    padding: 60px 0;
  }

  .Login form {
    margin: 0 auto;
    max-width: 320px;
  }
}
.NotFound {
    padding-top: 100px;
    text-align: center;
    font-size: 2.4rem;
  }

.appbar {
    background-color: #ddd5df;
  }
  a, .text-muted{
      font-size: 1.6rem;
  }
